"use client";

import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Checkbox } from "@/components/ui/checkbox";
import { Label } from "@/components/ui/label";
import { Input } from "@/components/ui/input";
import { getPermissions, updatePermission } from "../actions/permissions";

interface Permission {
  id: string;
  name: string;
  description: string;
}

export function PermissionsForm() {
  const [permissions, setPermissions] = useState<Permission[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function loadPermissions() {
      try {
        const data = await getPermissions();
        setPermissions(data);
      } catch (err) {
        setError("Failed to load permissions");
        console.error(err);
      } finally {
        setLoading(false);
      }
    }

    loadPermissions();
  }, []);

  const handleUpdate = async (permission: Permission) => {
    try {
      await updatePermission(permission.id, {
        name: permission.name,
        description: permission.description,
      });
    } catch (err) {
      console.error("Failed to update permission:", err);
    }
  };

  if (loading) {
    return <div>Loading permissions...</div>;
  }

  if (error) {
    return <div className="text-red-500">{error}</div>;
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Permissions Settings</CardTitle>
        <CardDescription>
          Configure user permissions and access levels
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div className="space-y-6">
          {permissions.map((permission) => (
            <div
              key={permission.id}
              className="space-y-2 p-4 border rounded-lg"
            >
              <div className="flex items-center justify-between">
                <Input
                  value={permission.name}
                  onChange={(e) => {
                    const updatedPermissions = permissions.map((p) =>
                      p.id === permission.id
                        ? { ...p, name: e.target.value }
                        : p
                    );
                    setPermissions(updatedPermissions);
                  }}
                  className="font-medium w-1/3"
                />
                <Button
                  onClick={() => handleUpdate(permission)}
                  size="sm"
                  variant="outline"
                >
                  Save
                </Button>
              </div>
              <Input
                value={permission.description}
                onChange={(e) => {
                  const updatedPermissions = permissions.map((p) =>
                    p.id === permission.id
                      ? { ...p, description: e.target.value }
                      : p
                  );
                  setPermissions(updatedPermissions);
                }}
                className="text-sm text-muted-foreground"
              />
            </div>
          ))}
        </div>
      </CardContent>
    </Card>
  );
}
